import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Hero } from './hero';

@Injectable()
export class HeroService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private heroesUrl = '/api/hero';  // URL to web api

  constructor(private http: Http) { }

  getHeroes(): Promise<Hero[]> {
    const url = `${this.heroesUrl}/all`;
    return this.http.get(url)
               .toPromise()
               .then(response=>response.json() as Hero[])
               .catch(this.handleError);
  }


  getHero(id: number): Promise<Hero> {
    const url = `${this.heroesUrl}/id?id=${id}`;
    return this.http.get(url)
      .toPromise()
      .then(response => response.json() as Hero)
      .catch(this.handleError);
  }

  delete(id: number): Promise<void> {
    const url = `${this.heroesUrl}/delete?id=${id}`;
    return this.http.get(url)
      .toPromise()
      .then(() => null)
      .catch(this.handleError);
  }

  create(hero: Hero): Promise<number> {
    console.log(hero.name);
    return this.http
      .post(this.heroesUrl, JSON.stringify(hero),{headers: this.headers})
      .toPromise()
      .then(res => res.json() as Hero)
      .catch(this.handleError);
  }

  update(hero: Hero): Promise<number> {
    const url = `${this.heroesUrl}/update`;
    const flag=0;
    return this.http
      .post(url, hero)
      .toPromise()
      .then(() => flag)
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}




